import React,{useState, createContext} from 'react'

export const TodoContext = createContext()

export function TodoProvider(props) {

    const [currentTodo, setCurrentTodo] = useState('')
    // const deleteIcon = <FontAwesomeIcon icon={faTrashAlt} />
    // const editIcon = <FontAwesomeIcon icon={faPencilAlt} />
    // const closeIcon = <FontAwesomeIcon icon={faTimes} />
    const [todos, setTodos] = useState([
        {
            id: 1,
            title: "Programmers count from Zero"
        },
        {
            id: 2,
            title: "It has CRUD functionality."
        },
        {
            id: 3,
            title: "It's a todo list app"
        }
    ])
    const [count, setCount] = useState(todos.length + 1)
    const [editATodo, setEditATodo] = useState('')
    const [modalShow, setModalShow] = useState(false)

    const [currentEditId, setCurrentEditId] = useState(0)

    return (
        <TodoContext.Provider value={[todos, setTodos]}>
            {props.children}
        </TodoContext.Provider>
    )
}
